@import url(https://fonts.googleapis.com/css2?family=Gemunu+Libre:wght@700&display=swap);
*,*::before,*::after{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  overflow-x: hidden;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}
:root{
  height: 100vh;
  width: 100vw;
  
}

.categories{
    position: relative;
    flex:0 0 30%;
    padding-top: 20px;
    overflow: visible;
}

.category__item{
    width: 100vw;
    padding-top: 20px;
    width: 100vw;
    height: 280px;
    display: flex;
    cursor: pointer;
}

.nested{
    display: inline-block;
    height: 100%;
    width: var(--vw);
    background-color: red;
    border:1px solid black;
    background-color: rgba(94, 0, 0, 0.918);
    position: relative;
    transition: all .5s ease-in;
    overflow: hidden;
    flex-shrink: 0;
    cursor: pointer;
}
.imageCategory{
    transition: all .5s ease-in-out;
}
.imageCategory:hover{
    transform: scale(1.1);
}

.slick-prev{
    background-color: transparent;
    z-index: 5;
    left:20px;

}
.slick-prev:active{
    transform: translateY(-5px);
}
.slick-next{
    background-color: transparent;
    z-index: 5;
    right:35px;
}
.slick-next:active{
    transform: translateY(-5px);
}
.hovereffect {
    width: 100%;
    height: 100%;
    float: left;
    overflow: hidden;
    position: relative;
    text-align: center;
    cursor: default;
  }
  
  .hovereffect .overlay {
    width: 100%;
    height: 100%;
    position: absolute;
    overflow: hidden;
    top: 0;
    left: 0;
  }
  
  .hovereffect img {
    display: block;
    position: relative;
    transition: all 0.4s ease-in;
  }
  
  .hovereffect:hover img {
    filter: url('data:image/svg+xml;charset=utf-8,<svg xmlns="http://www.w3.org/2000/svg"><filter id="filter"><feColorMatrix type="matrix" color-interpolation-filters="sRGB" values="0.2126 0.7152 0.0722 0 0 0.2126 0.7152 0.0722 0 0 0.2126 0.7152 0.0722 0 0 0 0 0 1 0" /><feGaussianBlur stdDeviation="3" /></filter></svg>#filter');
    filter: grayscale(1) blur(3px);
    -webkit-filter: grayscale(1) blur(3px);
    transform: scale(1.2);
  }
  
  .hovereffect h2 {
    text-transform: uppercase;
    text-align: center;
    position: relative;
    font-size: 17px;
    padding: 10px;
    background: rgba(0, 0, 0, 0.6);
  }
  
  .hovereffect .info {
    display: inline-block;
    text-decoration: none;
    padding: 7px 14px;
    border: 1px solid #fff;
    margin: 50px 0 0 0;
    background-color: transparent;
  }
  
  .hovereffect .info:hover {
    box-shadow: 0 0 5px rgb(32, 5, 155);
  }
  
  .hovereffect .info, .hovereffect h2 {
    transform: scale(0.7);
    transition: all 0.4s ease-in;
    opacity: 0;
    filter: alpha(opacity=0);
    color: #fff;
    text-transform: uppercase;
  }
  
  .hovereffect:hover .info, .hovereffect:hover h2 {
    opacity: 1;
    filter: alpha(opacity=100);
    transform: scale(1);
  }
  .hovereffect .delete-icon{
    cursor: pointer;
    position: absolute;
    bottom: 10%;
    right:10%;
    color: red;
    border: transparent;
    box-shadow: 0 0 5px rgb(182, 5, 5);
    border-radius: 5px;
  }
  .hovereffect .delete-icon:hover {
    box-shadow: 0 0 5px rgb(182, 5, 5);
  }
.item-details{
    display: flex;
    margin: 75px auto;
    padding: 0 40px;
}
.image{
    width: 300px;
    height: 300px;
}
.details{
    padding-left: 50px;
    display: flex;
    justify-content: space-between;
    flex-direction: column;
}
.details-header{
    display: flex;
    justify-content: space-between;
    flex-direction: column;
    flex: 0 0 50%;
}
.buttons-actions{
    width: 200px;
    display: flex;
    justify-content: space-between;
}

.input_form{
    display: flex;
    margin: 20px auto;
}
.modalClose{
    position: absolute;
    width: 30px;
    height: 30px;
    top: -15px;
    right: -10px;
    border-radius: 50%;
    outline: solid 1px black;
    box-shadow: rgba(0, 0, 0, 0.25) 0px 54px 55px, rgba(0, 0, 0, 0.12) 0px -12px 30px, rgba(0, 0, 0, 0.12) 0px 4px 6px, rgba(0, 0, 0, 0.17) 0px 12px 13px, rgba(0, 0, 0, 0.09) 0px -3px 5px;
}

.warningPassword{
    font-size: .8rem;
    margin-bottom: 3px;
}
.header{
    width:100%;
    height:65px;
    background-image: linear-gradient(to top right,rgb(96, 96, 248),rgb(26, 118, 240));
    display: flex;
    align-items: center;
    padding: 0 20px;
    justify-content: space-between;
}

.header__title{
    font-size: 35px;
    color: white;
    font-weight: 800;
    cursor: pointer;
}

.links{
    flex: 0 0 20%;
}

.headerLinks{
    text-decoration: none;
    color: white;
    font-size: 18px;
    margin: 0 10px;
    transition: all .5s ease-in-out;
}

.headerLinks:hover{
    color: brown;
    font-size: 22px;
}

.bodyMix{
    padding-bottom: 50px;
    display: flex;
    flex-direction: column;
    width: 100vw;
    background-color: rgb(233, 222, 207);
}


@media only screen and (max-width: 640px){
        .image__header{
            transform: translate(-200%,-50%) !important;
        }
}

.image__header{
    position: absolute;
    top:50%;
    left:50%;
    transform: translate(-50%,-50%);
    color: white;
    font-family: "Gemunu Libre";
    transition: all .4s ease-in;
    
}

.item_header{
    width: 100%;
    height: 150px;
    opacity: 0;
    top:100%;
    text-align: center;
    padding-top: 10%;
}

.image__header:hover{
    color:blue;
    
}

.items{
    flex: 0 0 70%;
    display: flex;
    flex-wrap: wrap;
    justify-content: space-around;
}

.item{
    max-width: 300px;
    height: 250px;
    background-color: rgba(48, 34, 102, 0.986);
    margin: 5px;
    box-shadow: rgba(0, 0, 0, 0.25) 0px 54px 55px, rgba(0, 0, 0, 0.12) 0px -12px 30px, rgba(0, 0, 0, 0.12) 0px 4px 6px, rgba(0, 0, 0, 0.17) 0px 12px 13px, rgba(0, 0, 0, 0.09) 0px -3px 5px;
    position: relative;
    flex: 0 0 30%;
}

.item:hover{
    cursor: pointer;
}


.item__price{
    text-align: center;
    font-size: 22px;
}

.item:hover>.item_header{
    cursor: pointer;
    opacity: 1;
    transform: translate(-50%,-100%);
    background-color: rgba(70, 57, 57, 0.623);
}

.noItems{
    padding-top: 50px;
    text-align: center;
}

.noItems>*{
    margin-top: 20px;
}

.category__header{
    text-align: center;
    margin-bottom: 10px;
    font-family: "Gemunu Libre";
    color: rgb(48, 3, 3);
    font-size: 35px;
}

.items__header{
    text-align: center;
    font-family: "Gemunu Libre";
    margin:0 30px;
}

.modal__open{
    width: 85px;
    height: 35px;
    margin-right: 35px;
    cursor: pointer;
    border-radius: 5px;
    border: 1px solid transparent;
    background-color: aliceblue;
    transition: all .3s ease-in;
}
.modal__open:active{
    transform: translateY(5px);
}

.loader{
    margin:150px auto;
}

.upper-top{
    position: absolute;
    top: 0;
    left: 50%;
    transform: translateX(-50%);
    z-index: 5;
    width: 100vw;
    height: 100vw;
    background-color: transparent;
}
.ReactModal__Content{
    overflow: visible !important;
}
.itemDelete{
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%);
    z-index: 5;
    width: 100vw;
    height: 100vw;
}
